@import url(https://fonts.googleapis.com/css?family=Raleway);
.style_filter-container__VfRxE {
    margin-top: -10vh;
}

.style_filter-title--container__2oaX9 {
    /* background-color:  rgb(29, 28, 28); */
    color: black;
    text-align: center;
    width: 50vw;
    padding: 10px;
}

.style_filter-title__eB7XU {
    font-family: 'Raleway', sans-serif;
    font-size: 30px;
}

.style_filter-close-btn__2s5CX {
    cursor: pointer;
    position: absolute;
    right: 20px;
    font-size: 30px;
    color: orangered;
}

.style_filter-content--container__2KPqA {
    padding: 10px;
    width:50vw;
    font-family: 'Raleway', sans-serif;
}

.style_filter-type--container__2eF_A {
    margin: 15px 0px;
    display: grid;
    grid-template-columns: repeat(4, minmax(40px, auto));
    grid-template-rows: repeat(2, minmax(30px, auto));
    grid-row-gap: 20px;
    font-size:1.2em;
}

.style_filter-content--title__ZCyZv {
    font-size:1.4em;
    margin-top:0px;
}
.style_filter-time--title__2Z9CZ{
    font-size:1.4em;
}
.style_filter-type-checkbox__3Ik01 {
    cursor: pointer;
    margin-top: 0px;
}

.style_filter-time--container__2f-Zc {
    width: 100%;
    margin: 20px 10px;
    margin-top: 30px;
    font-size:1.2em;
    display: flex;
    justify-content: flex-start;
}

.style_filter-time-checkbox__3G528 {
    cursor: pointer;
    margin-right: 20px;
}

@media only screen and (max-width: 1248px) {
    .style_filter-type--container__2eF_A {
        grid-template-columns: repeat(2, minmax(90px, auto));
        grid-template-rows: repeat(autofill, minmax(50px, auto));
    }  
    .style_filter-container__VfRxE {
        margin-top: 0vh;
    }  
    .style_filter-title__eB7XU {
        font-size: 28px;
    }
    
    .style_filter-close-btn__2s5CX {
        font-size: 28px;
    }

    .style_filter-content--title__ZCyZv ,.style_filter-time--title__2Z9CZ{
        font-size:1.3em;
    }
}

@media only screen and (max-width: 768px) {
    .style_filter-type--container__2eF_A {
        grid-template-columns: repeat(2, minmax(120px, auto));
        grid-template-rows: repeat(autofill, minmax(100px, auto));
        font-size: 16px;
    }

    .style_filter-container__VfRxE {
        margin-top: 0vh;
    }

    .style_filter-title__eB7XU {
        font-size: 27px;
    }
    
    .style_filter-close-btn__2s5CX {
        font-size: 27px;
    }

    .style_filter-content--title__ZCyZv ,.style_filter-time--title__2Z9CZ{
        font-size:1.3em;
    }
}

@media only screen and (max-width: 660px) {
    .style_filter-title--container__2oaX9 {
        width: 65vw;
    }

    .style_filter-content--container__2KPqA {
        width: 65vw;
    }

    .style_filter-container__VfRxE {
        margin-top: 0vh;
    }

    .style_filter-title__eB7XU {
        font-size: 25px;
    }
    
    .style_filter-close-btn__2s5CX {
        font-size: 25px;
    }

    .style_filter-content--title__ZCyZv ,.style_filter-time--title__2Z9CZ{
        font-size:1.2em;
    }

}

@media only screen and (max-width: 500px) {
    
    .style_filter-container__VfRxE {
        margin-top: 0vh;
    }
    .style_filter-title__eB7XU {
        font-size: 23px;
    }
    
    .style_filter-close-btn__2s5CX {
        font-size: 23px;
    }

    .style_filter-content--title__ZCyZv ,.style_filter-time--title__2Z9CZ{
        font-size:1.2em;
    }

    .style_filter-time--container__2f-Zc {
        flex-direction: column;
        font-size: 17px;
    }

}
@media only screen and (max-width: 450px) { 
   .style_filter-type--container__2eF_A {
      grid-template-columns: repeat(1, minmax(90px, auto));
      grid-template-rows: repeat(autofill, minmax(50px, auto));
      padding-left:12px;
    }  
    .style_filter-content--title__ZCyZv ,.style_filter-time--title__2Z9CZ{
        padding-left: 12px;
    }
}
